import Title from '@/components/Title'
import { Trim,setStore,getStore,removeStore } from "@/utils/util"
export default {
	data() {
		return {
			data:{},
		}
	},
	computed: {
		
	},
	watch:{
		
	},
	methods: {
		getData(){
			const OrderId = this.$route.query.OrderId;
			this.$http.post('/hotel/orderDetail',{OrderId:OrderId})
				.then((res) => {
					const status = res.data.status;
					if(status==1001){
						this.data = res.data.data.result;
						return
					}
					this.$notify.error({
			          title: '请求失败',
			          message: res.data.message,
			          duration: 3000
			        });
				})
		}
	},
	components:{
		Title,
	},
	created() {
		this.getData();
	}
}


